<template>
  <div class="home">
    <div v-for="item in list" :key="item.en">
      {{ item.en }} /{{ item.pronunciation }}/ {{ item.zh }}
      <div class="phraseSpeech">
        <a
          title="点击发音"
          href="javascript:;"
          class="pronounce"
          @click="onClick(item.en)"
        ></a>
      </div>
    </div>

    <Pagination
      class="pagination"
      v-model="currentPage"
      :page-count="21"
      :show-page-size="5"
      force-ellipses
      @change="onChange"
    />
  </div>
</template>

<script>
import Pagination from "vant/lib/pagination";
import "vant/lib/pagination/style";

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      currentPage: 1,
      list: [],
    };
  },
  async created() {
    this.getList(1);
  },
  methods: {
    getList(p) {
      this.list = require(`@/json/${p}.json`);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
    onClick(word) {
      var player = new Audio(
        `https://dict.youdao.com/dictvoice?audio=${word}&type=1`
      );
      player.play();
    },
    onChange(val) {
      this.getList(val);
    },
  },
};
</script>

<style scoped>
.home {
  padding-bottom: 40px;
}
.pagination {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
}
.phraseSpeech {
  display: inline-block;
  width: 22px;
  height: 22px;
  vertical-align: middle;
  font-size: 0;
  letter-spacing: 0;
}
.pronounce {
  display: block;
  width: 100%;
  height: 100%;
  background: url("@/assets/voice.png") 50% 50% no-repeat;
  background-size: contain;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
</style>
